import EXIF from './exif'
import opts from './opts'
import fastclick from './fastclick'
	//change
	opts.oFile.addEventListener('change', change, false);
	var oCan = opts.oCan,
		ctx = opts.ctx,
		isCreatePic = false,
		isChoose = false,
		oSelect = document.getElementById('selectPicSize'),
		oAsideBtn = document.querySelector('.asideBtn'),
		oAside = document.querySelector('#aside')
		//
		opts.oCreateBtn.addEventListener('touchstart',createImg,false)
	function createImg(e){
		e.preventDefault();
		if(!isCreatePic){return false;}
		//
		opts.oFile.parentNode.removeChild(opts.oFile)
	  this.parentNode.removeChild(this)
		oCan.style.display = 'none';
		this.style.display = 'block';
		//
		opts.oShowImg.src = oCan.toDataURL('image/png');
		alert(',');
		opts.oShowImg.style.display = 'block';
		opts.oShowImg.style.position = 'static';
	}

	var imgData = {

	}

	//load
	function cacheImg(url, callback) {
		if (!url) {
			callback.call()
			alert('false')
			return false;
		}
		var img = new Image()
		//img.crossOrigin = "Anonymous";
		img.onload = function() {
			//alert('load')
			//
			callback.call(img)
		}
		img.src = url;
	}

	function getImgData(params) {
		cacheImg(params.img,function(){
			var degree = 0,
				drawWidth, drawHeight, width, height
			//
			drawWidth = width = params.data.PixelXDimension ? params.data.PixelXDimension : this.naturalWidth;
			drawHeight = height = params.data.PixelYDimension ? params.data.PixelYDimension : this.naturalHeight;

			//
			//
			var maxSide = Math.max(drawWidth, drawHeight),
				size = 2048

			//
			if (maxSide > size) {
				var minSide = Math.min(drawWidth, drawHeight);
				minSide = minSide / maxSide * size;
				maxSide = size;
				if (drawWidth > drawHeight) {
					drawWidth  = maxSide;
					drawHeight  = minSide;
				} else {
					drawWidth  = minSide;
					drawHeight  = maxSide;
				}
			}
			//canvas
			var canvas=document.createElement('canvas');
			canvas.width=width=drawWidth;
			canvas.height=height=drawHeight;
			console.log('canvas width:' + canvas.width + 'canvas height:' +canvas.height)
			var context=canvas.getContext('2d');
			var orie = params.data.Orientation ? params.data.Orientation : false
			//canvasiphonehome
			switch (params.data.Orientation) {
				//iphonehome
				case 3:
					degree = 180;
					drawWidth = -width;
					drawHeight = -height;
					break;
					//iphonehome()
				case 6:
					canvas.width = height;
					canvas.height = width;
					degree = 90;
					drawWidth = width;
					drawHeight = -height;
					break;
					//iphonehome
				case 8:
					canvas.width = height;
					canvas.height = width;
					degree = 270;
					drawWidth = -width;
					drawHeight = height;
					break;
			}
			//canvas
			context.rotate(degree*Math.PI/180);
			context.drawImage(this,0,0,drawWidth,drawHeight);
				//
			params.next && params.next(canvas.toDataURL('image/png'));
		})
	}

	function change() {
		var file = this.files[0];
		var exifData;
		//
		if(file.size/1024 > 5000){
			//2M
			alert('')
			return false;
		}
		//
		opts.msg('block')


		//EXIF js  https://github.com/exif-js/exif-js
		EXIF.getData(file, function() {
			exifData = EXIF.getAllTags(this)
		});
		var reader = new FileReader();
		reader.onload = function(e) {
			//
			//
			getImgData({img:this.result,data: exifData,next:function(img){
				cacheImg(img,function(){
					//
					var w = this.width,
						h = this.height;
					//
					imgData.w = w;
					imgData.h = h;
					imgData.img = this;
					var cW = opts.canvasW,
						cH  = opts.canvasH;
					if(cW/cH > w/h){
						//100%
						w = w*cH/h
						h = cH;
					}else{
						h = h*cW/w
						w = cW;
					}
					ctx.clearRect(0,0,cW,cH);
					ctx.drawImage(this,(cW-w)/2,(cH-h)/2,w,h);
					opts.msg('none');
					//size
					opts.oCreateBtn.style.display = 'block';
					isCreatePic = true;
					opts.isDraw = true;
					opts.isNewPic = true;
				})
			}});
		}
		reader.readAsDataURL(file);
	}

	oSelect.addEventListener('change',function(e){
		var cW = opts.canvasW,
			cH = opts.canvasH,
			w = imgData.w,
			h = imgData.h,
			img = imgData.img
			
			ctx.clearRect(0,0,opts.canvasW,opts.canvasH);
		//
		switch (this.value) {
			case '1':
				type(true);
				ctx.drawImage(img,(cW-w)/2,(cH-h)/2,w,h);
				break;
			case '2':
				type(false);
				ctx.drawImage(img, (cW-w)/2 , (cH - h)/2 , w, h);
				break;
			case '3':
				type(true);
				ctx.drawImage(img,0,0,w,h);
				break;
			case '4':
				ctx.drawImage(img,0,0,cW,cH);
				break;
			default:
			type(true);
				ctx.drawImage(img,(cW-w)/2,(cH-h)/2,w,h);
			break;

			img = null;
		}

		function type(type){
			if(type){
				if(cW/cH > w/h){
					//100%
					w = w*cH/h
					h = cH;
				}else{
					h = h*cW/w
					w = cW;
				}
			}else{
				if(cW/cH <= w/h){
					//100%
					w = w*cH/h
					h = cH;
				}else{
					h = h*cW/w
					w = cW;
				}
			}
		}
	},false);

	oAsideBtn.addEventListener('touchstart',function(e){
		if(!isCreatePic){
			alert('')
			return false;}
		opts.isDraw = false;
		oAside.className = 'aside_show'
	})
	document.querySelector('.aside_hideBtn').addEventListener('touchstart',function(e){
		opts.isDraw = true;
		oAside.className = 'aside_hide'
	})

export default{

}


